import React, { useState, useContext } from "react";
import { Accordion, Card } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import { get_orders } from "../context/PublicOrderData";
import { OrdersContext, monthTable } from "../context/OrdersContext";
export default function Newsfeed() {
  const [orders, setOrders] = useContext(OrdersContext);
  const ordersArray = get_orders();

  return (
    <div className="newsfeed">
      <h2>Global News Feed</h2>
      <Accordion defaultActiveKey="1">
        {orders.sort(descending).map((month, ndx) => (
          <Card>
            <Accordion.Toggle
              as={Card.Header}
              eventKey={month.monthID.toString()}
            >
              {monthTable[month.monthID] + ", " + month.yearID + " AE"}
            </Accordion.Toggle>
            <Accordion.Collapse eventKey={month.monthID.toString()}>
              <Card.Body>
                {month.orders.map((report, ndx) => (
                  <div className="article">
                    <h1>{report.title}</h1>
                    <h3>{report.location}</h3>
                    <hr></hr>
                    <p>{report.description}</p>
                  </div>
                ))}
              </Card.Body>
            </Accordion.Collapse>
          </Card>
        ))}
      </Accordion>
    </div>
  );
}

function descending(a, b) {
  if (a.monthID === b.monthID) {
    return b.yearID - a.yearID;
  } else {
    return b.monthID - a.monthID;
  }
}
